.dp_relative {
  position: relative;
}

.dp_hide {
  display: none;
  visibility: hidden;
}

.dp_clear {
  clear: both;
}

.dp_always_visible {
  display: block !important;
  visibility: visible !important;
}

.dp_away {
  position: fixed;
  top: -10000px;
  left: -10000px;
}

.dp_middle_right {
  top: 13px;
  right: 15px !important;
  bottom: unset;
  left: unset;
}

.dp_success {
  color: #4caf50 !important;
}

.dp_danger {
  color: #f44336 !important;
}

.dp_link {
  color: #25b9d7 !important;
}

.dp_style .ui-tabs-panel {
  padding: 15px 5px !important;
}

.dp_center {
  text-align: center;
}

.dp_btn {
  background: none;
  border: none;
  cursor: pointer;
}