th.dp_drag {
  width: 20px;
  padding: 0 !important;
}

td.dp_drag {
  text-align: right;
  padding: 0 !important;
}

td.dp_drag span {
  cursor: move;
  display: block;
  background: transparent url(../../img/drag.png) no-repeat;
  width: 14px;
  height: 25px;
}

.dp_dragged > td {
  background: #81d4fa !important;
}
