#module_dynamicproduct {
  padding: 5px;
}

.dp_style .panel {
  padding: 10px;
  position: relative;
  display: block;
  background-color: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 0.25rem;
}

.dp_style .panel-heading {
  padding: 5px 10px;
  height: 32px;
  margin: -10px -10px 10px -10px;
  border-radius: 0.25rem 0.25rem 0 0;
  background-color: #f5f5f5;
  border-bottom: 1px solid #e5e5e5;
}

.dp_style .hidden {
  display: none !important;
  visibility: hidden !important;
}

.dp_style label {
  font-size: 13px;
}

.dp_style .btn {
  font-size: 12px;
}

.dp_style .btn-default {
  background: #FFF;
  color: #00AFF0;
  border-color: #00AFF0;
}

.dp_style .table td.center, .dp_style .table th.center {
  text-align: center;
}

.dp_style td {
  vertical-align: middle;
}

.dp_style .form-group {
  clear: both;
  padding: 5px;
  margin-bottom: 5px;
  margin-left: 25px;
}

.dp_style .alert {
  margin: 5px;
  padding-left: 3.875rem;
}

.dp_style .dp_equation_container {
  padding: 5px;
}

.dp_style .panel-footer {
  padding: 5px;
}

.dp_style .mce-edit-area {
  border: 1px solid #EEE !important;
  border-radius: 6px;
}

.dp_style .switch-toggle {
  outline: none;
  cursor: pointer;
  width: 160px;
}

.dp_style .switch-toggle * {
  cursor: pointer;
  outline: 0 solid #FFF !important;
}

.dp_style .badge {
  border: 1px solid #d9d9d9;
  border-radius: 20px;
  padding: 0 10px;
  font-weight: bold;
}

.dp_style .panel-heading-action {
  line-height: 0;
  position: absolute;
  top: 2px;
  right: 0;
}

.dp_style .panel-heading-action a.list-toolbar-btn {
  height: 27px;
  width: 30px;
  color: #ccc;
  float: left;
  border-left: solid 1px #eee;
  text-align: center;
  line-height: 27px;
}

.dp_style .panel .table {
  margin: 5px 0;
}

.dp_style #dp_dropdown_options_container .dp_lang_container,
.dp_style #dp_radio_options_container .dp_lang_container,
.dp_style #dp_thumbnails_options_container .dp_lang_container {
  margin-top: -2px;
}

.dp_style .col-lg-1 {
  width: 8.33333%;
  float: left;
}

.dp_style .col-lg-3 {
  width: 25%;
  float: left;
}

.dp_style .col-lg-4 {
  width: 33.33333%;
  float: left;
}

.dp_style .col-lg-8 {
  width: 66.66666%;
  float: left;
}

.dp_style .input-group-text {
  border-right-width: 0;
}

.dp_style .dp_no_border_left {
  border-left-width: 0;
}

/*-------------*/

.dp_style .material-icons {
  font-size: 16px;
}

.dp_on {
  color: #4caf50;
}

.dp_off {
  color: #f44336;
}

.dp_style .btn-success {
  color: #fff;
  background-color: #4CAF50;
  border-color: #4CAF50;
}

.dp_style .btn-danger {
  background-color: #F44336;
  border-color: #F44336;
}

.dp_style .btn-warning {
  background-color: #F0AF00;
  border-color: #F0AF00;
}

.dp_style a {
  text-decoration: none !important;
}

.dp_style a:hover {
  opacity: 0.8;
}

.dp_style a:active {
  opacity: 1;
}

.dp_style i:not([class^="mce-"]) {
  width: 16px;
  height: 16px;
  background-color: transparent;
  vertical-align: middle;
}

.dp_style .help-block {
  font-size: 12px;
  color: #555;
  margin: 15px 0;
}

.dp_style .switch-input + .help-block {
  margin-left: 49px;
}

.dp_style .switch-input {
  background-color: #607D8B;
}

.dp_style .switch-input.-checked {
  background-color: #3eb9d7;
}

.dp_style .switch-input, .dp_style .switch-input:after {
  transition-duration: 0.2s;
  border-color: #77c1d3;
  color: #77c1d3;
}

.dp_style .select2-container--bootstrap {
  display: inline-block;
}

.dp_style .card-header {
  font-weight: normal;

  i {
    margin-top: -1px;
    margin-right: 0;
  }
}

.dp_style kbd {
  background-color: #eee;
  border-radius: 3px;
  border: 1px solid #b4b4b4;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2), 0 2px 0 0 rgba(255, 255, 255, .7) inset;
  color: #333;
  display: inline-block;
  font-size: .85em;
  font-weight: 700;
  line-height: 1;
  padding: 2px 4px;
  white-space: nowrap;
}
