@import "field-types";

$colors: (
        $_DP_INPUT_      : #f44336,
        $_DP_FIXED_      : #2196f3,
        $_DP_PRICE_      : #9c27b0,
        $_DP_TEXT_       : #03a9f4,
        $_DP_DATE_       : #009688,
        $_DP_IMAGE_      : #4caf50,
        $_DP_PHP_        : #ffc107,
        $_DP_DROPDOWN_   : #9c27b0,
        $_DP_CHECKBOX_   : #2196f3,
        $_DP_SWITCH_     : #3f51b5,
        $_DP_FILE_       : #8bc34a,
        $_DP_SLIDER_     : #cd9321,
        $_DP_THUMBNAILS_ : #3f51b5,
        $_DP_TEXTAREA_   : #00bcd4,
        $_DP_FEATURE_    : #ff9800,
        $_DP_DIVIDER_    : #ff5722,
        $_DP_RADIO_      : #673ab7,
        $_DP_COLORPICKER_: #607d8b,
        $_DP_HTML_       : #3f51b5,
        $_DP_ERROR_      : #d0121a,
);

@each $type, $color in $colors {
  .dp_field_type_#{$type} {
    color: #{$color} !important;
  }
}
