#dp_tabs {
  font-size: 14px;
  padding: 0px;
  border-radius: 0px
}

.dp_style .ui-tabs .ui-tabs-panel {
  padding: 3px;
}

.dp_style .ui-tabs .ui-tabs-nav li a {
  padding: 7px 5px;
}

.dp_style .ui-widget-header {
  background: #fff;
  border: none;
  border-bottom: 1px solid #aaa;
  border-radius: 0px;
}

.dp_style .ui-tabs-nav li {
  font-size: 13px;
  border-radius: 0px;
  background: #F5F5F5;
}

.dp_style .ui-tabs-nav li.ui-tabs-active {
  background: #FFF;
}
