.dp_upload {
  position: relative;
  width: 35px;
  height: 35px;
  margin: 0 auto;
  overflow: hidden;
  cursor: pointer;
}

.dp_upload:not(.dp_empty):hover img {
  opacity: 0.2;
}

.dp_upload a {
  display: none;
}

.dp_upload:hover a {
  display: inline-block;
}

.dp_u_delete {
  z-index: 10001;
  position: absolute;
  top: 0;
  right: 0;
  color: #F00 !important;
}

.dp_u_external {
  z-index: 10001;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dp_u_color {
  display: none;
  visibility: hidden;
  z-index: 10001;
  position: absolute;
  bottom: 1px;
  right: 1px;
}

.dp_upload.dp_empty a:not(.dp_u_color) {
  display: none;
  visibility: hidden;
}

.dp_upload.dp_empty:hover a.dp_u_color {
  display: block;
  visibility: visible;
}

.dp_unit_values .dp_upload.dp_empty:hover a.dp_u_delete {
  display: block;
  visibility: visible;
}