div.dp_field_formula_container {
  min-height: 60px;
  font-size: 14px;
  position: relative;
  border: 1px solid #DEDEDE;
  background-color: #fff;
  padding: 10px;
  margin: 5px 0;
  border-radius: 3px;

  &:hover .btn-delete {
    display: block;
  }

  &.ui-sortable-placeholder {
    background-color: #DCEDC8;
  }
}

.dp_field_formula_container:nth-of-type(odd) {
  background-color: #F7F7F7;
}
