@import "partials/field-types-colors";
@import "partials/field-types-visibility";

$primary-color: #3eb9d7;

#dp_fields_table {
  border-bottom: none;
}

#dp_fields_table tr {
  height: 50px;

  &:hover {
    background-color: #DAF2FF;
  }
}

#dp_fields_table td {
  padding: 3px 2px;
}

#dp_fields_table select {
  padding: 4px 1px !important;
  height: 29px;
}

#dp_fields_table input {
  padding: 4px 5px !important;
}

.dp_fields_footer {
  padding: 10px;
}

td.id_field {
  text-align: center;
}

.dp_field_clone {
  display: none;
}

.dp_add_field {
  float: right;
}

tr.dp_favorite_field > td {
  background-color: #FF9800;
}

tr.dp_common_field > td {
  background-color: #81d4fa;
}

tr.dp_linked_field > td {
  background-color: #c5e1a5;
}

tr.dp_selected_field > td {
  background-color: #F44336;
}


.dp_fav {
  text-align: center;
  width: 40px !important;
  padding: 0 !important;
}

.dp_fav_button {
  display: none;
  color: #FF9800
}

#dp_fields_table tr:hover .dp_fav_button {
  display: inline-block;
}

.dp_common_btn {
  display: none;
  color: $primary-color
}

#dp_fields_table tr:hover .dp_common_btn {
  display: inline-block;
}

.dp_more_options_container {
  display: inline-block;
  position: relative;
  font-size: 14px;
  text-align: left;

  &.dp_show_list {
    .dp_more_options_list {
      display: inline-block;
    }

    .dp_more_options {
      background-color: #6c868e;
    }
  }
}

.dp_more_options_list {
  border: 1px solid $primary-color;
  background-color: #fff;
  position: absolute;
  top: 30px;
  right: 0;
  z-index: 1;
  _display: none;

  ul {
    margin: 0;
    padding: 0;

    li {
      list-style: none;
      border-bottom: 1px solid $primary-color;

      &:last-child {
        border-bottom: none;
      }
    }
  }

  button {
    width: 100%;
  }
}
