@import "field-types";

$name: "[data-name=name]";
$id_unit: "[data-name=id_unit]";
$settings: '.dp_field_settings_btn';
$value: '.dp_field_value';
$upload: '.dp_upload';

.dp_field_value {
  .input-group-addon {
    display: none;
  }
}

[type=field_#{$_DP_HTML_}],
[type=field_#{$_DP_ERROR_}]
{

  #{$upload} {
    display: none;
  }

}

[type=field_#{$_DP_FIXED_}] {

  #{$id_unit},
  #{$settings}
  {
    display: none;
  }

}

[type=field_#{$_DP_PRICE_}] {

  #{$id_unit},
  #{$settings}
  {
    display: none;
  }

  .dp_field_value {
    .input-group-addon {
      display: table-cell;
    }
  }

}

[type=field_#{$_DP_TEXT_}],
[type=field_#{$_DP_DATE_}],
[type=field_#{$_DP_IMAGE_}],
[type=field_#{$_DP_DROPDOWN_}],
[type=field_#{$_DP_FILE_}],
[type=field_#{$_DP_THUMBNAILS_}],
[type=field_#{$_DP_TEXTAREA_}],
[type=field_#{$_DP_FEATURE_}],
[type=field_#{$_DP_RADIO_}],
[type=field_#{$_DP_COLORPICKER_}],
[type=field_#{$_DP_HTML_}],
[type=field_#{$_DP_ERROR_}]
{

  #{$value},
  #{$id_unit}
  {
    display: none;
  }

}

[type=field_#{$_DP_PHP_}],
[type=field_#{$_DP_DIVIDER_}]
{

  #{$value},
  #{$id_unit},
  #{$settings}
  {
    display: none;
  }

}

[type=field_#{$_DP_CHECKBOX_}],
[type=field_#{$_DP_SWITCH_}] {
  #{$id_unit} {
    display: none;
  }
}
