/**************************\
  Basic Modal Styles
\**************************/

.dp_modal {
  display: none;
  opacity: 0;
  text-align: initial;

  .card {
    margin: 0;
    border: none;
  }

  .card-block {
    padding: 0;
  }

  label {
    color: #333;
    margin: 0;
    padding: 0 5px;
  }

  table {
    width: 100%;

    tr {
      height: auto !important;
    }
  }

  p {
    margin: 3px 0;
  }

  div.help-block {
    margin-bottom: 0;
  }
}

.dp_modal.is-open {
  opacity: 1;
  display: block;
}

.dp_modal__overlay {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 50px;
  overflow-x: hidden;
  overflow-y: auto;
}

.dp_modal__container {
  background-color: #fff;
  width: 800px;
  border-radius: 3px;
  box-sizing: border-box;
  margin-bottom: 50px;
}

.dp_modal__header {
  font-weight: bold;
  line-height: 1.5rem;
  padding: .625rem;
  background: #25b9d7;
  color: #fff;
  box-shadow: inset 0 -10px 15px -12px rgba(0, 0, 0, .3), 0 0 0 #555;
  border-bottom: none;
  border-radius: 3px 3px 0 0;

  i, .dp_modal__close {
    color: #fff;
  }
}

.dp_modal__title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25;
  color: #00449e;
  box-sizing: border-box;
}

.dp_modal__close {
  float: right;
  cursor: pointer;
  background: transparent;
  border: 0;
}

.dp_modal__close:before {
  font-size: 20px;
  content: "\2715";
}

.dp_modal__content {
  line-height: 1.5;
  color: rgba(0, 0, 0, .8);
}
