.dp_useful_links {
  display: flex;
  gap: 10px;
  justify-content: flex-end;

  &::after {
    content: "";
    clear: both;
    display: table;
  }
}

.dp_html_area {
  margin: 5px;
  border: 1px solid #25b9d7;

  label {
    padding: 5px;
  }
}

#dp_generic_equation {
  display: none;
}

.dp_unavailable {
  color: red !important;
}

.dp_chosen {
  display: inline-block;
}

.dp_style label.control-label {
  font-weight: bold;
}

.dp_style .table .form-group {
  text-align: left;
}
